<template>
  <li class="cosmoItem p10">
    <div class="cosmoItemHeader">
      <div class="cosmoItemImgContainer">
        <img :src="item.imgUrl" style='width:100%' alt="cosmoItemImg"/>
      </div>
      <h3 class="cosmoItemTitle">{{item.name}} </h3>
    </div>
    <div class="body" >
      <textarea
        v-model="item.text"
        class="cosmoTextArea"
        :style="`font-size:${item.fontSize||16}px`"
      />
      <div class="btnsContainer">
        <button class="saveBtn" @click="save" >save</button>
        <button class="saveBtn" @click="copyToTable(item)" v-if="$route.name==='cosmo'">copy to "Table"</button>
        <button class="saveBtn" @click="isOpenn=!isOpenn">{{isOpenn?'Close':'Open'}} rare data</button>
        <span>
          <button @click="changeSize(-2)" class="arrow">-</button>
          <span :style="`font-size:${item.fontSize}px`" >A {{item.fontSize}}</span>
          <button @click="changeSize(+2)" class="arrow">+</button>
        </span>
      </div>
    </div>
    <textarea
      v-model="computedItemValue"
      class="rareTextArea"
      v-if="isOpenn"
    />
    <div class="fc"><button @click="saveRare" class="saveBtn hoverNegative" v-if="isOpenn">Save manual fixes</button></div>
<!--    <span style="color:black">{{localObj}}</span>-->
  </li>
</template>

<script>
  import generateRandomId from '../../helpers/generateRandomId';

  export default {
    props:['item','list','currTab','listName','currSubTab'],
    data(){
      return{
        size:this.item.fontSize||16,
        localObj:{},
        isOpenn:false,
      }
    },
    computed:{
      computedItemValue:{//Стейт данные определенной закладки. Двунаправленное вычисляемое св-во
        get() {
          return JSON.stringify(this.item)
        },
        set(value) {
          // console.log('set', value)
          this.localObj = JSON.parse(value)
        }
      },
    },
    methods:{
      save(){
        this.$store.dispatch('updateFB',{name:this.listName,info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
      changeSize(number){
        const newNumber = this.item.fontSize?this.item.fontSize+number:16
        // console.log(newNumber)
        // this.$store.commit('setCosmoItemFontSize',{title:this.currTab,name:this.currSubTab,number:newNumber})
        if(this.computedItemValue.length!==0){
          const newCosmoList = this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName].map(item=>{
            if(item.title===this.currTab){
              const newItems = item.items.map(item2=> {
                if (item2.name === this.currSubTab) {
                  return {
                    ...item2,
                    fontSize:newNumber
                  }
                }else return item2
              })
              return {
                ...item,
                items:newItems
              }
            }else return item
          })
          this.$store.commit('setStateLevel1',{
            data:newCosmoList,
            nameLevel1:this.listName,
          })
        }
      },
      saveRare(){
        if(this.computedItemValue.length!==0){
          const newCosmoList = this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName].map(item=>{
            if(item.title===this.currTab){
              const newItems = item.items.map(item2=> {
                if (item2.name === this.currSubTab) {
                  return this.localObj
                }else return item2
              })
              return {
                ...item,
                items:newItems
              }
            }else return item
          })
          console.log(newCosmoList)
          this.$store.commit('setRare',{
            list:newCosmoList,
            listName:this.listName,
          })
        }
      },
      // moveLeft(val,array){
      //   console.log(val,array)
      //   let index = array.indexOf(val);
      //   array.splice(index,1)
      //   array.splice(index-1, 0, val)
      //   return array;
      // },
      // moveRight(val,array){
      //   let index = array.indexOf(val);
      //   array.splice(index,1)
      //   array.splice(index+1, 0, val)
      //   return array;
      // },
      copyToTable(item){
        const itemObj = {
          name:item.name,
          imgUrl:item.imgUrl,
          block:this.currTab,
          id:generateRandomId(),
        }
        this.$store.commit('addCosmoTableItem', {itemObj,listName:'cosmoTableList'})
      },

    }
  };
</script>

