<template>
  <section>
    <ul class="container p10 pr">
      <li class="grey fz12 fc" v-if="!cList.length">no table items</li>
      <li class="itemContainer pr" v-for="item in cList" :key="item.id" :item="item" :style="`padding-right:${isHide?'20':'0'}px`">
        <img class="img" :src="item.imgUrl" alt="ico" v-if="isHide"/>
        <span class="name" @click="isPlanItemOpen=!isPlanItemOpen" v-on:mousedown="(ev) => clickName(ev.target.innerText)">{{item.name}}</span>
        <input class="numberInput" type="text" v-model="item.number" v-on:keyup.enter="(ev) => {ev.target.blur();update()}">
        <button class="delBox" @click="deleteItem(item.id)" v-if="isHide">x</button>
        <button class="moveBtn left" @click="moveLeft(item,cList)" v-if="isHide"><</button>
        <button class="moveBtn right" @click="moveRight(item,cList)" v-if="isHide">></button>
      </li>
      <li>
        <button class="servBtn" @click="$store.commit('sortTable')" >sort up</button>
        <button class="servBtn" @click="isHide=!isHide" >short version ({{isHide?'off':'on'}})</button>
        <button class="servBtn" @click="isPlanSummaryOpen=!isPlanSummaryOpen" >daily plan ({{isPlanSummaryOpen?'on':'off'}})</button>
      </li>
    </ul>
    <div class="planContainerPop card pr" v-if="isPlanSummaryOpen">
      <p style="margin:20px auto; font-size: 24px;color:white">Daily plan summary</p>
      <p v-for="item in $store.state.infoMain.cosmoDailyPlanList" :key="item.name">
        <span class="mr10">{{item.name}}</span>
        <span class="mr10">{{item.time}}</span>
      </p>
      <button class="saveBtn" @click="save" >save</button>
      <button class="saveBtn" @click="reset" >reset</button>
      <button class="closeBtn" @click="isPlanSummaryOpen=!isPlanSummaryOpen">X</button>
    </div>
    <div class="planContainerPop card pr" v-if="isPlanItemOpen">
      <p>
        <span class="mr10">{{clickedName}}</span>
        <input class="numberInputPop" :key="key" type="text" v-model="time" v-on:keyup.enter="(ev) => {ev.target.blur();addPlanItem()} "/>
      </p>
      <button class="closeBtn" @click="isPlanItemOpen=!isPlanItemOpen">X</button>
    </div>
  </section>
</template>

<script>
  export default {
    data() {
      return {
        isHide:true,
        isPlanItemOpen:false,
        isPlanSummaryOpen:false,
        clickedName:'',
        time:'',
        key:0
      };
    },
    computed:{
      cList(){
        return this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].cosmoTableList
      }
    },
    methods: {
      update(){
        this.$store.dispatch('updateFB',{name:'cosmoTableList',info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
      deleteItem(id){
        this.$store.commit('deleteItem', {id, listName:'cosmoTableList'});
      },
      moveLeft(val,array){
        console.log(val,array)
        let index = array.indexOf(val);
        array.splice(index,1)
        array.splice(index-1, 0, val)
        return array;
      },
      moveRight(val,array){
        let index = array.indexOf(val);
        array.splice(index,1)
        array.splice(index+1, 0, val)
        return array;
      },
      clickName(name){
        this.key=this.key+1
        this.clickedName=name
      },
      addPlanItem(){
        this.$store.commit('addPlanItem',{name:this.clickedName, time:this.time})
        this.isPlanItemOpen=false
        this.time=''
      },
      save(){
        this.$store.dispatch('updateFB',{name:'cosmoDailyPlanList',info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
      reset(){
        this.$store.commit('resetDailyPlan')
        this.$store.dispatch('updateFB',{name:'cosmoDailyPlanList',info:this.$store.state.isAK?'infoAK':'infoMain'});
      }
    }
  };
</script>

