<template>
  <li class="cosmoItem pr p10" :class="{'cosmoItemOpened':isDropped}">
    <div class="cosmoItemHeader">
      <div class="cosmoItemImgContainer" @click="dropdown">
        <img :src="item.imgUrl" style='width:100%' alt="cosmoItemImg"/>
      </div>
      <div>
        <h3 class="cosmoItemTitle" @click="dropdown">{{item.name}} </h3>
      </div>
      <span class="arrow">
        <span @click="moveLeft(item,list)">&lt;</span>
        <span @click="moveRight(item,list)">&gt;</span>
      </span>
    </div>
    <div style="display:flex; flex-flow:column;" >
      <textarea
        v-model="item.text"
        class="cosmoTextArea"
        v-if="isDropped"
        :style="{ fontSize: size+'px'}"      />
      <div v-if="isDropped">
        <button class="saveBtn" @click="save" >save</button>
        <button class="saveBtn" @click="dropdown" >close</button>
        <button class="delBtn" @click="delItem(item.name)">X</button>
        <div class="sizeBtns">
          <span @click="size=size-2" class="minus mr20">-</span>
          <span @click="size=16">A</span>
          <span @click="size=size+2" class="plus ml20">+</span>
        </div>
      </div>
    </div>
  </li>
</template>

<script>
  export default {
    props:['item','currTab','listName', 'index','list'],
    data(){
      return{
        isDropped:false,
        size:16
      }
    },
    methods:{
      save(){
        this.$store.dispatch('updateFB',{name:this.listName,info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
      dropdown(){
        this.isDropped = !this.isDropped
      },
      delItem(itemName){
        const ok = confirm('delete this item?')
        if(ok){
          this.$store.commit('deleteNotesItem', { itemName, tab:this.currTab,listName:this.listName})
        }
      },
      moveLeft(val,array){
        let index = array.indexOf(val);
        array.splice(index,1)
        array.splice(index-1, 0, val)
        return array;
      },
      moveRight(val,array){
        let index = array.indexOf(val);
        array.splice(index,1)
        array.splice(index+1, 0, val)
        return array;
      },

    }
  };
</script>

