<template>
  <section class="container" :class="{'containerCosmobg':$route.name==='cosmo','containerNotesbg':$route.name==='notes'}">
    <ul class="bookmarksContainer hideMob">
      <li :class="['bookmarkBtn ', { active: currentTab === 'Дневник' }]" @click="currentTab = 'Дневник'" v-if="listName === 'cosmo'&&!$store.state.isAK">
        Дневник
      </li>
      <li :class="['bookmarkBtn', { active: currentTab === 'Table' }]" @click="currentTab = 'Table'" v-if="listName === 'cosmo'">
        Table
      </li>
      <li
        v-for="item in computedTabs"
        :key="item"
        :class="['bookmarkBtn', { active: currentTab === item}]"
        @click="tabClick(item)"
      >
        {{ item }}
      </li>
      <li v-if="!cList.length" class="grey fz12 fc">no catalog</li>
      <li class="servContainer">
        <div class="btnsContainer">
          <button class="addBtn hover" @click="addTab">add catalog</button>
          <button class="addBtn hover red" @click="deleteTab" v-if="currentTab!=='Дневник' && currentTab!=='Table'">del</button>
          <button class="addBtn hover" @click="moveLeft(cTabItem,cList)" v-if="currentTab!=='Дневник' && currentTab!=='Table'"><</button>
          <button class="addBtn hover" @click="moveRight(cTabItem,cList)" v-if="currentTab!=='Дневник' && currentTab!=='Table'">></button>
        </div>
        <input
          type="text"
          v-model="newTitle"
          v-on:keyup.enter="(ev) => {changeTitle();ev.target.blur()}"
          @focus="(()=>{newTitle=currentTab})"
          @focusout="()=>{newTitle=''}"
          :placeholder="currentTab"
          v-if="currentTab!=='Дневник' && currentTab!=='Table'&&isTabExist"
          class="tabInput"
          required
        >
      </li>
    </ul >
    <ul class="bookmarksContainer hideMob" v-if="isTabExist">
      <li
        v-for="item in computedSubTabList"
        :key="item.name"
        :class="['bookmarkSubBtn', { active: currentSubTab === item.name}]"
        @click="subTabClick(item.name)"
      >
        <img :src="item.imgUrl" alt="kjj" class="subImg"/>
        {{ item.name }}
      </li>
      <li v-if="!computedSubTabList.length" class="grey fz12 fc">no notes</li>
      <li class="servContainer">
        <div class="btnsContainer">
          <button class="addBtn hover" @click="addItem">add note</button>
          <button class="addBtn hover red" @click="deleteSub" v-if="currentTab!=='Дневник' && currentTab!=='Table'">del</button>
          <button class="addBtn hover" @click="moveLeft(computedSubTabItem,computedSubTabList)" v-if="currentTab!=='Дневник' && currentTab!=='Table'"><</button>
          <button class="addBtn hover" @click="moveRight(computedSubTabItem,computedSubTabList)" v-if="currentTab!=='Дневник' && currentTab!=='Table'">></button>
        </div>
        <input
          type="text"
          v-model="newName"
          v-on:keyup.enter="(ev) => {changeName();ev.target.blur()}"
          @focus="(()=>{newName=currentSubTab})"
          @focusout="()=>{newName=''}"
          :placeholder="currentSubTab"
          class="tabInput"
          v-if="isSubTabExist"
          required
        >
      </li>
    </ul>
    <select class="select hideDesk" type="text" name="catalog" v-model="currentTab">
      <option value="Дневник" v-if="listName === 'cosmo'&&!$store.state.isAK">Дневник</option>
      <option value="Table" v-if="listName === 'cosmo'">Table</option>
      <option :value="item" v-for="item in computedTabs" :key="item">{{item}}</option>
    </select>
    <select class="select hideDesk" type="text" name="catalog" v-model="currentSubTab">
      <option :value="item.name" v-for="item in computedSubTabList" :key="item.name">{{item.name}}</option>
    </select>
    <div class="bodyContainer pr">
      <ul class="cosmoList" v-if="isSubTabExist">
        <NotesItem2
          :item="computedSubTabItem"
          :list="computedSubTabList"
          :curr-tab="currentTab"
          :list-name="listName"
          :curr-sub-tab="currentSubTab"
        />
      </ul>
      <CosmoDiaryItem v-if="currentTab==='Дневник'&&$route.name==='cosmo'"/>
      <CosmoTable v-if="currentTab==='Table'&&$route.name==='cosmo'"/>
    </div>
  </section>
</template>

<script>
  import NotesList from './NotesList';
  import generateRandomId from '../../helpers/generateRandomId';
  import NotesItem2 from './NotesItem2';
  import CosmoDiaryItem from './CosmoDiaryItem';
  import CosmoTable from './CosmoTable';
  export default {
    props:['listName'],
    components: {
      NotesItem2,
      NotesList,
      CosmoDiaryItem,
      CosmoTable
    },
    data(){
      return{
        currentTab: this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName].length?this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName][0].title:null,
        currentSubTab: '',
        itemName:'',
        newTitle:'',
        newName:''
      }
    },
    computed:{
      computedTabs() {
        return this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName].length?this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName].map(item=>item.title):[];
      },
      computedSubTabList(){
        if(this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName].length){
          const obj1lvl = this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName].find(item=>item.title === this.currentTab)
          if(obj1lvl){
            return obj1lvl.items||[]
          }else return []
        }else return []
      },
      computedSubTabItem(){
        if(this.computedSubTabList.length){
          return this.computedSubTabList.find(item=>item.name===this.currentSubTab)
        }else return {}
      },
      cTabItem(){
        return this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName].find(item=>item.title === this.currentTab)||{}
      },
      cList(){
        return this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName]||[]
      },
      isTabExist(){
        return this.cList.find((item)=> {
          return item.title ===this.currentTab;
        });
      },
      isSubTabExist(){
        return this.computedSubTabList.find((item)=> {
          return item.name ===this.currentSubTab;
        });
      },
    },
    methods:{
      changeTitle(){
          const newCosmoList = this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName].map(item=>{
            if(item.title===this.currentTab){
              return {
                ...item,
                title:this.newTitle
              }
            }else return item
          })
        console.log('newCosmoList',newCosmoList)
          this.$store.commit('setStateLevel1',{
            data:newCosmoList,
            nameLevel1:this.listName,
          })
        this.currentTab=this.newTitle
        this.$store.dispatch('updateFB',{name:this.listName,info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
      changeName(){
          const newCosmoList = this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName].map(item=>{
            if(item.title===this.currentTab){
              const newItems = item.items.map(item2=> {
                if (item2.name === this.currentSubTab) {
                  return {
                    ...item2,
                    name:this.newName
                  }
                }else return item2
              })
              return {
                ...item,
                items:newItems
              }
            }else return item
          })
          this.$store.commit('setStateLevel1',{
            data:newCosmoList,
            nameLevel1:this.listName,
          })
        this.currentSubTab=this.newName
        this.$store.dispatch('updateFB',{name:this.listName,info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
      addTab() {
        const title = prompt('Название закладки')
        const item = {
          title,
          id:generateRandomId(),
          items:[]
        }
        if (title) {
          this.$store.commit('addNotesTab', { item,listName:this.listName})
          this.currentTab = title;
        }
      },
      addItem() {
        const name = prompt('Название заметки')
        const itemObj = {
          name,
          imgUrl:'/img/'+this.listName+'.png',
          text:'',
          id:generateRandomId(),
        }
        if (name) {
          this.$store.commit('addNotesItem', {itemObj,tab:this.currentTab,listName:this.listName})
          this.currentSubTab = name;
        }
      },
      deleteTab(){
        const ok = confirm('delete tab '+this.currentTab+' ?')
        if(ok){
          this.$store.commit('deleteNotesTab', { tabName:this.currentTab, listName:this.listName})
          let lastTab
          if(this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName].length>0){
            lastTab = this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName][this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'][this.listName].length-1].title
          }else lastTab = ''
          this.currentTab = lastTab ;//сдвинуть таб посл стр
        }
      },
      deleteSub(){
        const ok = confirm('delete '+this.currentSubTab+' ?')
        if(ok){
          this.$store.commit('deleteNotesItem', { itemName:this.currentSubTab, tab:this.currentTab,listName:this.listName})
          let lastSubTab
          if(this.computedSubTabList.length>0){
            lastSubTab = this.computedSubTabList[this.computedSubTabList.length-1].name
          }else lastSubTab = ''
          this.currentSubTab = lastSubTab ;//сдвинуть таб посл стр
        }
      },
      saveTab(){
        this.$store.dispatch('updateFB',{name:this.listName,info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
      // addItem() {
      //   const itemObj = {
      //     name:this.itemName,
      //     imgUrl:'/img/'+this.listName+'.png',
      //     text:'',
      //     id:generateRandomId()
      //   }
      //   this.$store.commit('addNotesItem', {itemObj,tab:this.currentTab,listName:this.listName})
      //   this.itemName = ''
      // },
      tabClick(tab){
        this.currentTab = tab;
        this.currentSubTab = '';
      },
      subTabClick(tab){
        this.currentSubTab = tab;
      },
      moveLeft(val,array){
        // console.log(val,array)
        let index = array.indexOf(val);
        array.splice(index,1)
        array.splice(index-1, 0, val)
        return array;
      },
      moveRight(val,array){
        let index = array.indexOf(val);
        array.splice(index,1)
        array.splice(index+1, 0, val)
        return array;
      },

    }
  };
</script>

