<template>
  <ul class="container" v-if="!$store.state.isAK">
    <li class="grey fz12 fc" v-if="!cList.length">no diary items</li>
    <li class="item pr">
      <input type="text" class="inputComment" style="width: 90px" placeholder="date" v-model='formDate' v-on:keyup.enter="(ev) => {ev.target.blur();addItem()}" required/>
      <input type="text" class="inputComment" style="display: inline-block" placeholder="comment" v-model="formText"  v-on:keyup.enter="(ev) => {ev.target.blur();addItem()}" required>
    </li>
    <li class="item pr" v-for="item in cList" :key="item.id" :item="item">
      <span class="grey mr20" >{{item.date}}</span>
      <input type="text" v-model="item.text" class="comment" v-on:keyup.enter="(ev) => {ev.target.blur();update()}">
      <button @click="deleteItem(item.id)" class="delBox">x</button>
    </li>
  </ul>
</template>

<script>
  import generateRandomId from '../../helpers/generateRandomId';
  export default {
    data() {
      return {
        formText:'',
        formDate:new Date().toLocaleString('ru-RU', { year: 'numeric', month: '2-digit', day: '2-digit' }),
      };
    },
    computed:{
      cList(){
        return this.$store.state.infoMain.cosmoDiaryList
      }
    },
    methods: {
      addItem() {
        const itemObj = {
          date:this.formDate,
          text:this.formText,
          id:generateRandomId()
        }
        this.$store.commit('addDiaryItem', {itemObj,listName:'cosmoDiaryList'})
        this.formText = ''
      },
      update(){
        this.$store.dispatch('updateFB',{name:'cosmoDiaryList',info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
      deleteItem(id){
        this.$store.commit('deleteItem', {id, listName:'cosmoDiaryList'});
      },
    }
  };
</script>

