  }
  .inputComment{
    color: rgba(255, 255, 255, 0.7);
    background-color: transparent;
    border:1px solid rgba(255, 255, 255, 0.2);
    width:80%;
  }
  .item {
    font-size: 14px;
    margin-bottom:5px;
    border: 1px solid rgba(255,255,255,0.05);
  }
  .addBtn:hover {
    cursor:pointer;
    color:white;
  }

  .delBox{
    position: absolute;
    right:0;
